.footer-container {
    background-color: #242424;
    padding-left: 30px;
    padding-top: 10px;
    margin-top: 30px;
    display:flex;
    flex-direction: column;
    justify-content: space-evenly

  }

.footer__top__links {
    display: flex;
    justify-content: flex-start;
}

.footer__top__links__items {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 16px;
    text-align: left;
    width: 160px;
    box-sizing: border-box;
  }

.footer__top__links__items > h2 {
    margin-bottom: 16px;
}

.footer__top__links__items > h2 {
    color: #fff;
  }

.footer__top__links a {
    color: #fff;
    text-decoration: none;
    margin-bottom: 0.9rem;
}

.footer-link-items a:hover {
    color: grey;
    transition: 0.3s ease-out;
}
.footer__bottom{
    display:flex;
    border-top: 1px solid  lightgray;
    padding: 20px;
    align-items: center;
    text-align: center;
    justify-content: space-between;

}

.footer__bottom__terms {
    display:flex;
    align-items: center;
}
.footer__bottom__terms__labels{
    padding:5px;
    font-size:14px;
    color: #fff;
}

.footer__bottom__socialIcons {
    display:flex;
    align-items: center;
    justify-content: flex-end;
    width:30%;
}
@media (max-width: 800px) {
    .footer__bottom {
      flex-direction: column;
      justify-content:flex-start;
      align-items:flex-start
    }
    .footer__bottom__terms {
        justify-content:flex-start;
        flex-direction: column;
        align-items: flex-start;
    }
    .footer__bottom__terms > a {
        height:2em;
        text-decoration:none;

    }
    .footer__bottom__socialIcons {
        margin-top: 2em;
        display:flex;
        align-items: center;
        justify-content: flex-start;
        width:30%;
    }
    .footer__bottom__terms__labels{
        padding:1px;
        font-size:14px;
        color: #fff;
        text-decoration:none;
    }

}

